import './Home.css'
import React from 'react';
import airplane from '../../../assets/images/static/home/airoplane.png';
import imgSection2 from '../../../assets/images/static/home/section-2.jpg';
import imgSection3 from '../../../assets/images/static/home/section-3.jpg';
import imgSection4 from '../../../assets/images/static/home/section-4.jpg';
import imgSection5 from '../../../assets/images/static/home/section-5.jpg';
import imgSection6_1 from '../../../assets/images/static/home/section-6.1.jpg';
import imgSection6_2 from '../../../assets/images/static/home/section-6.2.jpg';
import imgSection6_3 from '../../../assets/images/static/home/section-6.3.jpg';
import {NavLink} from "react-router-dom";

const Home = () => {
    return <>
        <section className={'section-hero'}>
            <div className="hero-content">
                <h1 className={'main-heading'}>
                    <span>EVOLUTIONARY NIGHT</span>
                    <span>VISION TECHNOLOGY</span>
                </h1>
                <NavLink to={'/'} className={'btn btn-dark btn-learn-more'}>Learn more</NavLink>
                <h4 className={'question-header'}>What are you looking for ?</h4>
                <div className="input-group">
                    <button className={"btn btn-light btn-search"}>
                        <i className="icon-search"></i>
                    </button>
                    <input type="text" className="form-control" placeholder={'Search from website'}/>
                </div>
            </div>
            <div className="plane-img-container">
                <img src={airplane} alt="Airplane"/>
            </div>
        </section>
        <section className={'section-content section-dark'}>
            <div className="container-fluid">
                <div className="row">
                    <div className="col-lg-7">
                        <div className="img-holder left">
                            <img src={imgSection2} alt="Engineering"/>
                        </div>
                    </div>
                    <div className="col-lg-5">
                        <div className="content-container">
                            <h2 className={'sub-heading'}>Engineering</h2>
                            <p>
                                At Aeroverse, we are the trusted disruptors in aviation engineering, driving innovation
                                to new heights. With our team of skilled professionals, we deliver groundbreaking
                                solutions that transform the industry. From advanced component design and systems
                                integration to pioneering technologies like night vision, we are at the forefront of
                                engineering excellence.
                            </p>
                            <p>
                                Our relentless pursuit of groundbreaking advancements allows us to shape the future of
                                aviation, setting new standards for safety, performance, and efficiency. Join us on this
                                extraordinary journey and be a part of redefining the way we fly. Together, let's soar
                                to new heights of engineering ingenuity.
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <section className={'section-content section-light'}>
            <div className="container-fluid">
                <div className="row">
                    <div className="col-lg-5">
                        <div className="content-container">
                            <h2 className={'sub-heading'}>Business and operations</h2>
                            <p>
                                In the realm of business and operations, Aeroverse takes flight as a leader in the
                                aerospace industry. With unwavering passion and commitment, our dedicated team supports
                                the diverse missions of our customers, spanning aerospace manufacturing, defense, and
                                space exploration. Through our comprehensive suite of services, including supply chain
                                management, logistics, procurement, and project execution, we optimize operations and
                                ensure seamless coordination.
                            </p>
                            <p>
                                By leveraging our expertise, we provide tailored solutions that maximize efficiency,
                                reduce costs, and drive operational excellence. With a strong focus on customer
                                satisfaction and safety, Aeroverse establishes enduring partnerships, delivering
                                exceptional value in the dynamic world of aerospace business and operations.
                            </p>
                        </div>
                    </div>
                    <div className="col-lg-7">
                        <div className="img-holder right">
                            <img src={imgSection3} alt="Business and operations"/>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <section className={'section-content section-black'}>
            <div className="container-fluid">
                <div className="row">
                    <div className="col-lg-7">
                        <div className="img-holder left">
                            <img src={imgSection4} alt="New Grads and Interns"/>
                        </div>
                    </div>
                    <div className="col-lg-5">
                        <div className="content-container">
                            <h2 className={'sub-heading'}>New Grads and Interns</h2>
                            <p className={'text-uppercase'}>
                                As we embark on the next chapter of innovation in the aerospace industry, Aeroverse is
                                thrilled to open its doors to new graduates and interns. We believe in providing a
                                supportive environment where emerging talents can thrive and contribute to our mission
                                of advancing the aerospace field. As a new grad or intern at Aeroverse, you'll have the
                                opportunity to work alongside experienced professionals, gaining hands-on experience and
                                exposure to cutting-edge projects.
                            </p>
                            <p className={'text-uppercase'}>
                                We offer mentorship programs, comprehensive training, and a collaborative culture that
                                encourages innovation and growth. Join us to launch your career and make a meaningful
                                impact on the future of aviation.
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <section className={'section-content section-black'}>
            <div className="container-fluid">
                <div className="row">
                    <div className="col-lg-5">
                        <div className="content-container">
                            <h2 className={'sub-heading'}>Enhancement Network</h2>
                            <p className={'text-uppercase'}>
                                In the world of aerospace, an efficient and robust enhancement network is essential for
                                driving innovation and progress. At Aeroverse, we understand the significance of a
                                well-connected ecosystem that fosters collaboration and amplifies capabilities. Our
                                Enhancement Network is designed to bring together industry leaders, research
                                institutions, and technology partners to collectively push the boundaries of aerospace
                                advancements. Through strategic partnerships and knowledge sharing, we harness the power
                                of collective intelligence to tackle complex challenges and explore new possibilities.
                            </p>
                            <p className={'text-uppercase'}>
                                The Aeroverse Enhancement Network serves as a catalyst for breakthrough innovations,
                                enabling us to deliver cutting-edge solutions that redefine the future of aerospace.
                            </p>
                        </div>
                    </div>
                    <div className="col-lg-7">
                        <div className="img-holder right">
                            <img src={imgSection5} alt="Enhancement Network"/>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <section className={'section-home-news'}>
            <div className="container-fluid">
                <div className="row">
                    <div className="col-lg-4">
                        <div className="card card-news">
                            <img src={imgSection6_1} alt="Aeroverse to Develop US Air Force"/>
                            <div className="card-body">
                                <p className={'category'}>Press Release</p>
                                <h2 className={'post-title'}>Aeroverse to Develop US Air Force </h2>
                                <p className="post-excerpt text-uppercase">
                                    AT AEROVERSE, WE PUT YOU IN THE DRIVER’S SEAT OF
                                    YOUR CAREER. AEROVERSE IS COMMITTED TO FOSTERING
                                    RELATIONSHIPS THAT SUPPORT THE INNOVATORS OF THE
                                    FUTURE
                                </p>
                                <NavLink to={'/'} className="btn btn-dark btn-sm">Go somewhere</NavLink>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4">
                        <div className="card card-news">
                            <img src={imgSection6_2} alt="Aeroverse to Develop US Air Force"/>
                            <div className="card-body">
                                <p className={'category'}>Press Release</p>
                                <h2 className={'post-title'}>Aeroverse to Develop US Air Force </h2>
                                <p className="post-excerpt text-uppercase">
                                    AT AEROVERSE, WE PUT YOU IN THE DRIVER’S SEAT OF
                                    YOUR CAREER. AEROVERSE IS COMMITTED TO FOSTERING
                                    RELATIONSHIPS THAT SUPPORT THE INNOVATORS OF THE
                                    FUTURE
                                </p>
                                <NavLink to={'/'} className="btn btn-dark btn-sm">Go somewhere</NavLink>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4">
                        <div className="card card-news">
                            <img src={imgSection6_3} alt="Aeroverse to Develop US Air Force"/>
                            <div className="card-body">
                                <p className={'category'}>Press Release</p>
                                <h2 className={'post-title'}>Aeroverse to Develop US Air Force </h2>
                                <p className="post-excerpt text-uppercase">
                                    AT AEROVERSE, WE PUT YOU IN THE DRIVER’S SEAT OF
                                    YOUR CAREER. AEROVERSE IS COMMITTED TO FOSTERING
                                    RELATIONSHIPS THAT SUPPORT THE INNOVATORS OF THE
                                    FUTURE
                                </p>
                                <NavLink to={'/'} className="btn btn-dark btn-sm">Go somewhere</NavLink>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </>;
};

export default Home;
